<template lang="">
  <div>
    <!-- 여러개의 함수를 호출 -->
    <button type="button" @click="one(), two()">click</button>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    one() {
      alert("one");
    },
    two() {
      alert("two");
    },
  },
};
</script>
<style lang=""></style>
